:root {
  --font-body: Arial, sans-serif;
  --font-inter: "Inter", sans-serif;
  --font-lexend: "Lexend Deca", sans-serif;
}

:root {
  --black: hsl(0, 0%, 0%);
  --verydarkblue: hsl(233, 47%, 7%);
  --darksaturatedblue: hsl(244, 38%, 16%);
  --softviolet: hsl(277, 64%, 61%);
  --white: hsl(0, 0%, 100%);
  --whitemain: hsla(0, 0%, 100%, 0.75);
  --whitestat: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-inter);
  font-size: 15px;
  background: var(--verydarkblue);
  color: var(--white);
  margin: 0;
  padding: 0;
}

.attribution {
  font-size: 0.6875rem;
  text-align: center;
  margin-bottom: 1.25rem;
}

.attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  border-radius: 0.3125rem;
  padding: 0 1.5rem;
  max-width: 72.375rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.card {
  max-width: 20.4375rem;
  margin: 0 auto;
  margin-top: 5.5rem;
  margin-bottom: 1.25rem;
  background: var(--darksaturatedblue);
  display: flex;
  flex-direction: column;
  border-radius: 0.3125rem;
  overflow: hidden;
}
@media (min-width: 65em) {
  .card {
    width: 100%;
    max-width: max-content;
    height: 27.875rem;
    flex-direction: row-reverse;
    margin-top: 11.0625rem;
  }
}
.card__image {
  background-color: var(--softviolet);
  background-blend-mode: multiply;
  background-image: url("../images/image-header-mobile.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
  width: 100%;
  height: 15rem;
}
@media (min-width: 65em) {
  .card__image {
    background-image: url("../images/image-header-desktop.jpg");
    width: 33.75rem;
    height: auto;
  }
}
.card__details {
  margin-bottom: 1.5375rem;
}
@media (min-width: 65em) {
  .card__details {
    width: 35.625rem;
    height: 100%;
  }
}
.card__title {
  margin-top: 2.5rem;
  text-align: center;
  font-size: 1.75rem;
  width: 100%;
  padding: 0 1.875rem;
}
@media (min-width: 65em) {
  .card__title {
    line-height: 2.75rem;
    font-size: 2.25rem;
    max-width: 24.0625rem;
    margin-top: 4.4375rem;
    margin-left: 4.5rem;
    padding: 0;
    text-align: left;
  }
}
.card__title span {
  color: var(--softviolet);
}
.card__description {
  color: var(--whitemain);
  margin: 0 auto;
  margin-top: 1.375rem;
  padding: 0 2.1875rem;
  line-height: 1.5625rem;
  text-align: center;
}
@media (min-width: 65em) {
  .card__description {
    padding: 0;
    text-align: left;
    margin-left: 4.5rem;
    max-width: 24.0625rem;
  }
}
.card__stat {
  margin-top: 2.9375rem;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  text-align: center;
  height: 13.375rem;
}
@media (min-width: 65em) {
  .card__stat {
    text-align: left;
    flex-direction: row;
    margin-top: 4.4375rem;
    margin-left: 4.5rem;
    max-width: 22.0625rem;
  }
}
.card__stat-num {
  margin-bottom: 0.25rem;
  font-size: 1.5rem;
  font-weight: 700;
}
.card__stat-name {
  color: var(--whitestat);
  text-transform: uppercase;
  font-size: 0.8125rem;
}
@media (min-width: 65em) {
  .card__stat-name {
    font-family: var(--font-lexend);
  }
}